import request from "./request.js"

/* 
	description: 
	params:
        userId: String,
*/
const getResume = (userId) => request(`/resume/${userId}`,"GET",{})

/* 
	description: 
	input: resume: Object,
*/
const putResume = (data) => request("/resume","PUT",data)



export {
  getResume,
	putResume,
}