import request from "./request.js"

/*  */
const getNotice = () => request("/notice","GET",{})
/*  */
const getFeedbackMe = (param) => request("/feedback/me","GET",param)
/*  */
const feedbackProblem = (data) => request("/feedback","POST",data)

export{
	getNotice,
	getFeedbackMe,
	feedbackProblem
}