import Vue from 'vue'
const baseUrl = getApp().globalData.baseUrl

function myRequest(url,method,data){
	/*  */
	const token = uni.getStorageSync("token") || ''
	const headers = {
		"Content-Type": "application/json; charset=utf-8",
		'Authorization': token
	}
	
	return new Promise((res,rej) => {
		uni.request({
			url: baseUrl + url,
			method: method,
			data: data,
			header: headers,
			success(result) 
			{
				/*  */
				if(result.data.code === 200)
				{
					// console.log(result.header)
					res(result.data)
				}
				/*  */
				else
				{
					console.log("")
					console.log(result.data)
					Vue.prototype.gToastError(result.data.msg)
					/* token */
					if(result.data.code === 103){
						uni.clearStorageSync("token")
						uni.reLaunch({
							url: "/pages/Login/Login",
							success: () => {
								Vue.prototype.gToastError(result.data.msg)
							}
						})
					}
					rej(result.data)
				}
			},
			fail(err) 
			{
				console.log("")
				console.log(err.data)
				Vue.prototype.gToastError("")
				rej(err)
			},
			complete(result) {
				/* token,token */
				if(result.header.Authorization){
					console.log(result.header);
					uni.setStorageSync("token",result.header.Authorization)
				}
			}
		})
	})
}
export default myRequest